.web__navbar {

display: flex;
justify-content: space-between;
align-items: center;

padding: 2rem 6rem;

}

.web__navbar-links{

flex: 1;
display: flex;
justify-content: flex-start;
align-items: center;


}

.web__navbar-links_logo  {

margin-right: 2rem;

}

.web__navbar-links_logo img {

    width: 62.56px;
    height: 16.02px;
    
 }

 .web__navbar-links_container {
    display: flex;
    flex-direction: row;

 }

 .web__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
 }

 .web__navbar-links_container p,
 .web__navbar-sign p,
 .web__navbar-menu_container p
 


 {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    
    margin: 0 1rem;
    cursor: pointer;
 }

 .web__navbar-sign button,
 .web__navbar-menu_container button

 {
    padding: 0.5rem 2rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 24.59px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;

    }

    .web__navbar-menu {

        margin-left: 1rem;
        display: none;
        position: relative;
            }

        .web__navbar-menu svg {

            cursor: pointer;

        }

        .web__navbar-menu_container

        {

            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            flex-direction: column;

            text-align: end;
            background: var(--color-footer);
            padding: 2rem;
            position: absolute;
            top: 40px;
            right: 0;
            margin: 1rem;
            min-width: 210px;
            border-radius: 5px;
            box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);

        }

    .web__navbar-menu_container p {

        margin: 1rem 0;

    }

    .web__navbar-menu_container-links-sign {

        display: none;

    }


    @media screen and (max-width: 1050px) {
        .web__navbar-links_container {
            display: none;
        }

        .web__navbar-menu {
            display: flex;
        }
    }

    @media screen and (max-width: 700px) {
        .web__navbar {
            padding: 2rem 4rem;
        }

    }
    
    @media screen and (max-width: 550px) {
        .web__navbar {
            padding: 2rem;
        }

        .web__navbar-sign  {
            display: none;

        }

        .web__navbar-menu_container {
            top: 20px;
        }

        .web__navbar-menu_container-links-sign{

            display: block;
        }
    }